															class="form-control"
															type="text"
															value=""
															:class="[{'is-invalid': errors.fname}]"
															@keydown="clearErrors($event.target.name)"
															v-model="itemData.fname">
														<div class="invalid-feedback">
															{{ errors.fname }}
