													<path d="M3.88 19.4v4.22h4.21l12.45-12.44-4.22-4.22L3.87 19.4zM23.8 7.92c.44-.44.44-1.15 0-1.59L21.17 3.7a1.12 1.12 0 0 0-1.59 0l-2.06 2.06 4.22 4.22 2.06-2.06z" fill="#fff"/>
